body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.6;
  font-family: 'Fira Sans', sans-serif;
  overflow-x: hidden;
  margin: 0;
  padding: 0; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

a:active,
a:visited,
a:focus {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: insert(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::after,
*::before {
  box-sizing: border-box; }

button {
  cursor: pointer; }

.flex {
  display: flex; }
  .flex_jc-sb {
    justify-content: space-between; }
  .flex_a-c {
    align-items: center; }

.container {
  margin: 100px auto;
  width: 620px;
  border-radius: 10px; }

.filter-header {
  height: 45px;
  background-color: #ff8663;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px; }
  .filter-header__title {
    margin: 0;
    padding: 0;
    color: #fff;
    font-size: 16px;
    font-weight: 500; }
  .filter-header__close {
    background-position: center;
    background-image: url("../img/icon-close-header.png"); }

.filer-search {
  height: 45px;
  background-color: #f0f0f0; }
  .filer-search input {
    height: 25px;
    width: 48%;
    padding-left: 13px;
    font-size: 12px;
    color: #000;
    font-family: inherit;
    font-weight: 300;
    border-radius: 30px;
    border: none; }
    .filer-search input::placeholder {
      color: #9f9f9f; }
  .filer-search .input_img {
    padding-left: 25px;
    background-repeat: no-repeat;
    background-image: url("../img/icon-search.png");
    background-position: 8px center; }

.filter-body .left-column,
.filter-body .right-column {
  width: 50%; }

.filter-body .left-column {
  padding-right: 15px;
  position: relative; }
  .filter-body .left-column::after {
    position: absolute;
    content: "";
    top: 0;
    right: -1px;
    width: 1px;
    height: 100%;
    background-color: #f0f0f0; }

.filter-body__title {
  margin: 0;
  margin-top: 13px;
  padding-bottom: 4px;
  border-bottom: 1px solid #f0f0f0; }

.friends-wrapper {
  max-height: 320px;
  height: 100%;
  overflow-y: auto; }

.friends-item {
  height: 60px;
  overflow: hidden;
  cursor: move; }
  .friends-item:hover {
    background-color: #f0f0f0; }
  .friends-item_active {
    background-color: #f0f0f0; }
  .friends-item__img {
    margin-right: 5px;
    margin-left: 5px; }
    .friends-item__img img {
      margin: auto;
      width: 45px;
      height: 45px;
      border-radius: 50%;
      display: inline-block; }
  .friends-item__title {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    color: #4f4f4f; }
  .friends-item__button {
    margin-left: auto;
    margin-right: 7px; }

.button {
  position: relative;
  border: none;
  background-color: transparent;
  transition: all .2s; }
  .button::after {
    position: absolute;
    content: "";
    top: 50%;
    transform: translateY(-50%) rotate(0deg);
    right: 0;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/icon-add-friends.png"); }

.right-column {
  padding-left: 15px; }
  .right-column .button::after {
    transform: translateY(-50%) rotate(45deg); }

.filter-footer {
  height: 45px;
  background-color: #f0f0f0; }
  .filter-footer button {
    width: 86px;
    height: 30px;
    margin-left: auto;
    color: #fff;
    font-weight: 300;
    font-family: inherit;
    border: none;
    border-radius: 15px;
    background-color: #ff8663; }

.button-reset {
  margin: 0;
  padding: 0;
  font-size: 0;
  width: 16px;
  height: 16px;
  background-color: transparent;
  border: none; }

.p {
  padding-left: 15px;
  padding-right: 15px; }
